.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#Submit {
  background-color: blue;
  height: 3em;
  color: white;
  font-size: 2em;
  border: none;
  border-radius: 20px;
  padding: 1em;
}

textarea {
  height: 200px;
  width: 50%;
  font-size: 2em;
  border: 8px solid black;
  padding: 1em;
}
